import { Button, Flex, Popconfirm, Space, Table, Typography } from 'antd';

import { useCopyToClipboardFunction } from 'hooks/useCopyToClipboardFunction';
import { UseResourceFirebaseDataReturnType } from 'hooks/useResourceFirebaseData';
import { orderBy, shuffle } from 'lodash';
import { DailyMovieSet, type Item as ItemT } from 'types';
import { removeDuplicates } from 'utils';

import type { TableProps } from 'antd';
import { ItemsTypeahead } from './ItemsTypeahead';
import { DeleteFilled } from '@ant-design/icons';
import { useTDResource } from 'hooks/useTDResource';
import { Item } from 'components/Sprites';
import { useMemo } from 'react';
import { ItemName } from './ItemBuildingBlocks';
function orderSets(givenSets: DailyMovieSet[]) {
  return orderBy(givenSets, [
    // (s) => removeDuplicates(s.itemsIds).filter(Boolean).length > 0,
    (s) => s.title,
  ]).map((s) => ({
    ...s,
    itemsIds: orderBy(s.itemsIds, (id) => Number(id)),
  }));
}

type Crime = {
  id: string;
  suspect: ItemT;
  weapon: ItemT;
  evidence: ItemT;
};

export function ItemsCrimeHistoryTable() {
  const itemsTypeaheadQuery = useTDResource<ItemT>('items');

  const suspects = useMemo(
    () =>
      shuffle(
        Object.values(itemsTypeaheadQuery.data ?? []).filter((item) => item.categories?.includes('suspect'))
      ),
    [itemsTypeaheadQuery.data]
  );

  const weapons = useMemo(
    () =>
      shuffle(
        Object.values(itemsTypeaheadQuery.data ?? []).filter((item) => item.categories?.includes('weapon'))
      ),
    [itemsTypeaheadQuery.data]
  );

  const evidence = useMemo(
    () =>
      shuffle(
        Object.values(itemsTypeaheadQuery.data ?? []).filter((item) => item.categories?.includes('evidence'))
      ),
    [itemsTypeaheadQuery.data]
  );

  const crimes = useMemo(() => {
    const total = Math.max(suspects.length, weapons.length, evidence.length);
    const crimesArray: Crime[] = [];
    for (let i = 0; i < total; i++) {
      crimesArray.push({
        id: `crime-${i}`,
        suspect: suspects[i],
        weapon: weapons[i],
        evidence: evidence[i],
      });
    }
    return crimesArray;
  }, [suspects, weapons, evidence]);

  const columns: TableProps<Crime>['columns'] = [
    {
      title: 'Suspect',
      dataIndex: 'suspect',
      render: (item) => (
        <Flex gap={6} vertical>
          {item ? (
            <>
              <Item id={item.id} width={60} />
              <ItemName item={item} language="pt" />
              <ItemName item={item} language="en" />
            </>
          ) : (
            <>?</>
          )}
        </Flex>
      ),
    },
    {
      title: 'Means of Murder',
      dataIndex: 'weapon',
      render: (item) => (
        <Flex gap={6} vertical>
          {item ? (
            <>
              <Item id={item.id} width={60} />
              <ItemName item={item} language="pt" />
              <ItemName item={item} language="en" />
            </>
          ) : (
            <>?</>
          )}
        </Flex>
      ),
    },
    {
      title: 'Evidence',
      dataIndex: 'evidence',
      render: (item) => (
        <Flex gap={6} vertical>
          {item ? (
            <>
              <Item id={item.id} width={60} />
              <ItemName item={item} language="pt" />
              <ItemName item={item} language="en" />
            </>
          ) : (
            <>?</>
          )}
        </Flex>
      ),
    },
  ];

  return (
    <Space direction="vertical">
      <Table columns={columns} rowKey="id" dataSource={crimes} />
    </Space>
  );
}

type AddItemFlowProps = {
  movie: DailyMovieSet;
  addEntryToUpdate: (id: string, item: DailyMovieSet) => void;
};

function AddItemFlow({ movie, addEntryToUpdate }: AddItemFlowProps) {
  const onUpdate = (itemId: string) => {
    addEntryToUpdate(movie.id, {
      ...movie,
      itemsIds: [...movie.itemsIds, itemId],
    });
  };

  return (
    <div>
      <ItemsTypeahead onFinish={onUpdate} />
    </div>
  );
}

type RemoveItemFlowProps = {
  movie: DailyMovieSet;
  addEntryToUpdate: (id: string, item: DailyMovieSet) => void;
  itemId: string;
};

function RemoveItemFlow({ movie, addEntryToUpdate, itemId }: RemoveItemFlowProps) {
  const onRemove = () => {
    addEntryToUpdate(movie.id, {
      ...movie,
      itemsIds: movie.itemsIds.filter((id) => id !== itemId),
    });
  };

  return (
    <Popconfirm
      title="Are you sure you want to remove this item?"
      onConfirm={onRemove}
      okText="Yes"
      cancelText="No"
    >
      <Button icon={<DeleteFilled />} size="small" type="text" ghost />
    </Popconfirm>
  );
}
